.modal-fade {
  transition: all 400ms cubic-bezier(0.25, 0.8, 0.25, 1);
}

.modal-fade-exited {
  opacity: 0.01;
  display: none;
  z-index: -10;
}

.modal-fade-entering {
  display: flex;
  opacity: 0.01;
}

.modal-fade-entering.modal-fade-enter-active {
  opacity: 1;
  z-index: 10;
}

.modal-fade-exiting {
  opacity: 1;
  z-index: 10;
}

.modal-fade-exiting.modal-fade-exit-active {
  opacity: 0.01;
  z-index: -10;
}

