.toast-fade {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  transition: all 400ms cubic-bezier(0.25, 0.8, 0.25, 1);
}

.toast-fade-exited,
.toast-fade-entering,
.toast-fade-exiting.toast-fade-exit-active {
  opacity: 0;
  z-index: -1;
}

.toast-fade-exited.hasFooterBar,
.toast-fade-exited.noFooterBar,
.toast-fade-entering.hasFooterBar,
.toast-fade-entering.noFooterBar,
.toast-fade-exiting.toast-fade-exit-active.hasFooterBar,
.toast-fade-exiting.toast-fade-exit-active.noFooterBar {
  transform: translate3D(0, 0, 0);
}

.toast-fade-entered,
.toast-fade-exiting,
.toast-fade-entering.toast-fade-enter-active {
  opacity: 1;
  z-index: 100;
}

.toast-fade-entered.hasFooterBar,
.toast-fade-exiting.hasFooterBar,
.toast-fade-entering.toast-fade-enter-active.hasFooterBar {
  transform: translate3D(0, -65px, 0);
}

.toast-fade-entered.noFooterBar,
.toast-fade-exiting.noFooterBar,
.toast-fade-entering.toast-fade-enter-active.noFooterBar {
  transform: translate3D(0, -16px, 0);
}

